@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Roboto:wght@300&display=swap);
* {
    font-family: 'Poppins', sans-serif;
}
*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
.side-heading {
    text-align: center;
    
}

.containerOne {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100vw;
    z-index: 1;
    background-color: #ffffff;
    padding: 10px 0;
    top: 0%;
    border-bottom: 1px solid rgb(186, 186, 186);
}
.boxOne {
    width: 95%;
    height: 50px;
    padding: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.boxOne .navContent{
    background-color: #5b4cfa;
    border-radius: 10px;
    padding: 10px 10px;
}

.first-boxOne, .first-boxOne .navContent{
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.navContent p{
    margin: 0;
}
.first-boxOne{
    margin-left: 50px;
}
.second-boxOne{
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 30px;
    align-items: center;
    color: #4060bf;
}

.linksOne{
    text-decoration: none;
    color: whitesmoke;
    font-size: 14px;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}
.linksOne:hover{
    text-decoration: none;
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    color: white;
}
.linksTwo {
    text-decoration: none;
    color: #5b4cfa;
    font-size: 15px;
    padding: 5px 8px;
    border: 1px solid #5b4cfa;
    border-radius: 10px;
}
.linksTwo:hover{
    color: #3728db;
    padding: 4px 7px;
    text-decoration: none;
    border: 2px solid #5b4cfa;
}

.first-boxOne img {
    width: 110px;
    margin-top: 10px;
}


.firstSection-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 120vh;
  background-color: #ffffff;
}
.firstSection-main-one {
  width: 80%;
  height: 40vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 8%;
}
.firstSection-main-one-first .first-heading {
  font-size: 38px;
  font-weight: lighter;
}
.firstSection-main-one-first .first-heading strong {
  font-weight: 900;
}

.firstSection-main-one-second {
  width: 45%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.firstSection-main-one-second .second-button{
    text-decoration: none;
    color: white;
    font-size: 15px;
    padding: 5px 8px;
    border-radius: 5px;
    border: none;
    background-color: #5b4cfa;
    cursor: pointer;
}
.firstSection-main-one-first div{
  display: flex;
  justify-content: space-between;
}
.firstSection-main-one-second .first-section-img{
  margin-left: 50px;
  margin-right: 30px;
  padding: 20px;
  border: 3px solid black;
  border-image: linear-gradient(to left, #5b4cfa, #5b4cfa) 1 0;
}
.firstSection-words {
  padding: 10px;
  color: white;
  border-radius: 5px 0px;
}
.blue-txt{
  color: #5b4cfa;
  font-size: 14px;
  font-weight: bold;
}
.orange-txt {
  color: #f38a1a;
  font-size: 14px;
  font-weight: bold;
}
.firstSection-combIconTxt{
  margin-right: 50px;
}
.firstSection-combIconTxt i{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right:10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.firstSection-combIconTxt .one {
  color: #5b4cfa;
  border: 1px solid #5b4cfa;
}

.firstSection-combIconTxt .two {
  color: #f38a1a;
  border: 1px solid #f38a1a;
}
.firstSection-main-two{
  display: flex;
  margin: 5% 10% 5% 13%;
}
.cards-card {
  /* background-color: #F6F9FE; */
  display: inline-block;
  width: 17rem;
  height: 10rem;
  margin: 3%;
  /* transition: transform 0.2s; */
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  padding: 20px 0 0 0;
}
.cards-card:hover{
  /* transform: scale(1.03); */
}
.cards-card p {
  font-size: 13px;
  font-weight: bold;
}
.cards-card i {
  color: #0093D0;
}

.cardsIcons{
  font-size: 25px;
}
