.grid {
	position: relative;
	text-align: center;
}

/* Common style */
.grid figure {
    position: relative;
    overflow: hidden;
    background: #3085a3;
    text-align: center;
}
.grid figure img {
	position: relative;
	display: block;
	min-height: 100%;
	max-width: 100%;
}

.grid figure figcaption {
	color: #fff;
	text-transform: uppercase;
	font-size: 1.25em;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}


.grid figure figcaption, .grid figure figcaption > a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(32, 71, 129, 0.42);
}
.grid figure figcaption:hover {
    background: rgba(22, 63, 124, 0.7);
}
/* Anchor will cover the whole item by default */
/* For some effects it will show as a button */
.grid figure figcaption > a {
	z-index: 1000;
	text-indent: 200%;
	white-space: nowrap;
	font-size: 0;
	opacity: 0;
}

.grid figure h2 {
	word-spacing: -0.15em;
	font-weight: 300;
}

.grid figure h2 span {
	font-weight: 800;
}

.grid figure h2,
.grid figure p {
	margin: 0;
}

.grid figure p {
    letter-spacing: 1px;
    font-size: 68.5%;
    color: #eee;
    padding-top: 1em;
}

/* Individual effects */
/******************* Timeline Demo - 2 *****************/
.main-timeline2 {
    padding-top: 50px;
    overflow: hidden;
    position: relative
}

.main-timeline2:before {
    content: "";
    width: 7px;
    height: 100%;
    background: #0093d0;
    margin: 0 auto;
    position: absolute;
    top: 0px;
    left: 0;
    right: 0
}

.main-timeline2 .timeline {
    width: 50%;
    float: left;
    padding: 20px 60px;
    border-top: 7px solid #0093d0;
    border-right: 7px solid #0093d0;
    border-radius: 0 30px 0 0;
    position: relative;
    right: -3.5px
}

.main-timeline2 .icon {
    display: block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    background: #02b3e4;
    border: 1px solid #fff;
    text-align: center;
    font-size: 25px;
    color: #fff;
    box-shadow: 0 0 0 2px #02b3e4;
    position: absolute;
    top: -30px;
    left: 0
}

.main-timeline2 .timeline-content {
    display: block;
    padding: 30px;
    border-radius: 20px;
    background: #02b3e4;
    color: #fff;
    position: relative;
}

.main-timeline2 .timeline-content:hover {
    text-decoration: none;
    color: #fff
}

.main-timeline2 .timeline-content:after,
.main-timeline2 .timeline-content:before {
    content: "";
    display: block;
    width: 10px;
    height: 50px;
    border-radius: 10px;
    background: #02b3e4;
    border: 1px solid #fff;
    position: absolute;
    top: -35px;
    left: 50px
}

.main-timeline2 .timeline-content:after {
    left: auto;
    right: 50px
}

.main-timeline2 .title {
    font-size: 1em;
    text-transform: capitalize;
    color: #000;
    margin-bottom: 1em;
    font-weight: 600;
    background: #fff;
    border-radius: 5px;
    display: inline-block;
    padding: 6px 20px;
}

.main-timeline2 .description {
    font-size: 0.9em;
    color: #fff;
}

.main-timeline2 .timeline:nth-child(2n) {
    border-right: none;
    border-left: 7px solid #0093d0;
    border-radius: 30px 0 0;
    right: auto;
    left: -3.5px
}

.main-timeline2 .timeline:nth-child(2n) .icon {
    left: auto;
    right: 0;
    box-shadow: 0 0 0 2px #a951ed
}

.main-timeline2 .timeline:nth-child(2) {
    margin-top: 130px
}

.main-timeline2 .timeline:nth-child(odd) {
    margin: -130px 0 30px
}

.main-timeline2 .timeline:nth-child(even) {
    margin-bottom: 80px
}

.main-timeline2 .timeline:first-child,
.main-timeline2 .timeline:last-child:nth-child(even) {
    margin: 0 0 30px
}

.main-timeline2 .timeline:nth-child(2n) .icon,
.main-timeline2 .timeline:nth-child(2n) .timeline-content,
.main-timeline2 .timeline:nth-child(2n) .timeline-content:after,
.main-timeline2 .timeline:nth-child(2n) .timeline-content:before {
    background: #a951ed
}

.main-timeline2 .timeline:nth-child(3n) .icon,
.main-timeline2 .timeline:nth-child(3n) .timeline-content,
.main-timeline2 .timeline:nth-child(3n) .timeline-content:after,
.main-timeline2 .timeline:nth-child(3n) .timeline-content:before {
    background: #02ccba
}

.main-timeline2 .timeline:nth-child(3n) .icon {
    box-shadow: 0 0 0 2px #02ccba
}

.main-timeline2 .timeline:nth-child(4n) .icon,
.main-timeline2 .timeline:nth-child(4n) .timeline-content,
