.container{
   margin-top: 100px;
}
.container h2{
    margin-left: 80px;
}
.cards{
    background: #F6F9FE;
    margin: 40px 30px 40px 30px;
    padding: 30px;
    border-radius: 15px;
}
.cards1{
    display: flex;
}
.cards1 i{
    color: #5041ec;
    padding: 8px 20px 0px 20px;
    font-weight: bold;
}
.cards1 p{
    color: #5041ec;
    font-size: 20px;
    font-weight: bold;
}
.cards2{
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
}
.cards2 p{
    width: 70%;
}
.buyBtn{
    cursor: pointer;
    background-color: #5b4cfa;
    height: 40px;
    color: white;
    padding: 10px;
    border-radius: 20px;
    border: none;
    width: 200px;
}
.buyBtn:hover{
    opacity: 0.9;
}